import widgetBackgroundColor from '@/widget/background-color'

const thickness = 20

export default {
  components: {
    widgetBackgroundColor
  },
  data () {
    return {
      thickness
    }
  },
  computed: {
    top () {
      return -thickness
    },
    left () {
      return -thickness
    },
    width () {
      return window.screen.width + thickness * 2
    },
    height () {
      return window.screen.height + thickness * 2
    },
    style () {
      const {
        top,
        left,
        width,
        height
      } = this
      return {
        top: `${top}px`,
        left: `${left}px`,
        width: `${width}px`,
        height: `${height}px`
      }
    }
  }
}



