
import PopupPicker from 'vux/src/components/popup-picker/index.vue'


let resolve

export default {
components: {
PopupPicker
},
data () {
return {
show: false,
title: '',
data: []
}
},
created () {
window.sdk.picker = async ({ title = '', data }) => {
this.title = `${title}`
this.data = data
this.show = true
let promise
({ promise, resolve } = window.util.promise())
const result = await promise
return result
}
},
methods: {
change (value) {
this.show = false
resolve({
value
})
},
hide (bool) {
this.show = false
if (bool) {

} else {
resolve({
index: -1
})
}
}
}
}



