
import Confirm from 'vux/src/components/confirm/index.vue'


let resolve

export default {
components: {
Confirm
},
data () {
return {
show: false,
title: '',
content: ''
}
},
created () {
window.sdk.confirm = async ({ title = '', content = '' }) => {
this.show = true
this.title = `${title}`
this.content = `${content}`
let promise
({ promise, resolve } = window.util.promise())
const result = await promise
return result
}
},
methods: {
confirm () {
resolve({
index: 1
})
},
cancel () {
resolve({
index: 0
})
}
}
}



